@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
.heroes__list{position:relative}.heroes__item-enter{transform:scale(0)}.heroes__item-enter-active{transition:all 200ms ease-in;transform:scale(1)}.heroes__item-exit{opacity:1;transition:all 250ms ease-in}.heroes__item-exit-active{opacity:0}.heroes__text{position:absolute;left:0;right:0;margin:0 auto}.heroes__text-enter{opacity:0;z-index:-1}.heroes__text-enter-active{opacity:1;transition:opacity 100ms ease-in;z-index:1}.heroes__text-exit{opacity:1;transition:opacity 10ms ease-in;z-index:1}.heroes__text-exit-active{opacity:0;z-index:-1}
.spinner-dots{margin:0 auto;display:flex;align-items:center}
.filters__item-enter{opacity:0}.filters__item-enter-active{opacity:1;transition:opacity 250ms ease-in}.filters__item-exit{opacity:1;transition:opacity 250ms ease-in}.filters__item-exit-active{opacity:0}.btn-group{width:100%}
.app{height:100vh;overflow-y:scroll;background:url(/heroes-app/static/media/hero_bg.0540b794.jpg) center/cover no-repeat}.content{display:grid;grid-template-columns:560px 440px;max-width:1040px;margin:0 auto;padding-top:50px}.content__interactive{padding-left:40px}
*{margin:0;padding:0;box-sizing:border-box;font-family:"Roboto",sans-serif}ul{padding:0}
